@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
img {
  max-width: 100%;
  object-fit: cover;
}
.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
.button {
  font-family: "PT Sans", sans-serif;
  display: block;
  text-align: center;
  color: #ffffff;
  background-color: #81b3d2;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0;
  margin: 0 auto;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}
.main-nav {
  width: 100%;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #ffffff;
  }
}
.main-nav__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    width: 1200px;
  }
}
@media (min-width: 768px) {
  .main-nav__item:nth-child(3),
  .main-nav__item:nth-child(7) {
    grid-column-start: 4;
  }
}
.main-nav__link {
  display: block;
  padding-top: 17px;
  padding-bottom: 11px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  font-style: normal;
  text-align: center;
  background-color: #81b3d2;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.main-nav__link:hover {
  background-color: #669ec0;
}
.main-nav__link:active {
  background-color: #5496bd;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__link {
    background-color: #ffffff;
    color: #000000;
    font-size: 14px;
    line-height: 21px;
    padding-top: 18px;
    padding-bottom: 17px;
    border: none;
  }
  .main-nav__link--current {
    color: #766357;
  }
}
@media (min-width: 768px) {
  .main-nav__item a[href]:hover {
    color: #81b3d2;
    background-color: #ffffff;
  }
  .main-nav__item a[href]:active {
    color: #000000;
    background-color: #ffffff;
    opacity: 0.3;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 11px;
  right: 14px;
  z-index: 4;
  display: block;
  width: 32px;
  height: 32px;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 6px;
  right: 6px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.3), 0 18px 0 0 rgba(129, 179, 210, 0.3);
}
.main-nav--opened .main-nav__toggle {
  top: 17px;
  right: 10px;
  left: auto;
  background-color: transparent;
}
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
  width: 32px;
  height: 32px;
}
.main-nav--opened .main-nav__toggle:hover::after {
  opacity: 60%;
}
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 30%;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--opened {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 3;
    min-height: 20vh;
    background-color: #81b3d2;
  }
}
@media (max-width: 767px) {
  .main-nav--opened::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 7px;
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs {
    position: static;
    min-height: 0;
    margin-top: 96px;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__item {
    display: block;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs .user-list__item {
    display: block;
    background-color: #81b3d2;
  }
}
